import {mapActions, mapState} from "vuex"

export default {
    data () {
        return {
            drawer: true,
            mini: true,
            items: [
                { title: 'Administradores', icon: 'mdi-home', name:"administradores" ,children: [] },
            ],
            
        }
    }, computed:{
        ...mapState({  
            user: state => state.user
        }),
    }, methods: {
        ...mapActions([
            'getUser'
        ]),
        routerTo(name){
            if(this.$route.name == name){
                return
            }
            this.$router.push({name: name});
        },
        logout(){
            localStorage.clear();
            this.$router.push({name: 'login'});


        }
    }
}
