import {RULES} from "./../../../plugins/rules";
import Croopie from "../../../plugins/Croppie.vue";
import {mapActions, mapState} from "vuex"
export default {
    name:"PageForm",
    props:['row'],
    mixins: [RULES],
    components: {
        Croopie
    },
    data(){
        return{
            action: 0,
            load: false,
            form:{
                id:null,
                nombre: null,
                apellidos: null,
                correo: null,
                area_id: null,
                estatus: null
            },
            file: null,
            namePhoto: "no-imagen.png",
            overlay: false,
        }
    }, mounted(){
        this.action = 0; // update
        if(this.row != null){
            this.action = 1; // update
            this.form = Object.assign({}, this.row)
            this.namePhoto = this.form.image
        }
        this.load = true;
    },
    computed:{
        ...mapState({  
            areas: state => state.areas,
            estatus: state => state.estatus,
        }),
    }, beforeMount(){
        this.getAreas();
    }, methods:{
        ...mapActions([
            'getAreas'
        ]),
        open(page){
            this.$emit('page', {page:page, row:null})
        },
        onLoadImage(file) {
            try {
                this.file = file;
                this.urlPhoto = URL.createObjectURL(file);
                this.$refs.crop.upload_file(this.urlPhoto);
            } catch (error) {
                console.error(error)
            }
        },
        async store(){
            try{
                let valid = this.$refs.admin.validate();
                if (!valid) {
                    return false;
                }
                this.overlay = true;
                let form = new FormData();

                if(this.form.id == null){
                    delete this.form.id;
                }

                for(let key in this.form){
                    form.append(key, this.form[key]);
                }
                if (this.file != null) {
                    form.append("upload", this.file);
                }
                let {data} = await this.$axios.post('administrador/store', form);
                this.overlay = false;
                if(!data.response){
                    return;
                }
                this.open('success');    
            }catch(exception){
                this.overlay = false;
                console.error(exception)
            }
        },
    }
}
