import {mapActions, mapState} from "vuex"
export default {
    name:'PageMain',
    props:['row'],
    data () {
        return {
            query:{
                name: null,
                area: null,
                estatus: null
            },
            search: '',
            loading: true,
            count: 0,
            rows: [],
            options: {},
            menu: false,
            headers: [
                { text: 'Administrador', value:'fullname', sortable: false  },
                { text: 'Área', value: 'area.nombre' },
                { text: 'Correo electrico', value: 'correo', },
                { text: 'Estatus', value: 'estado', sortable: false },
                { text: 'Detalles', value: 'action', sortable: false }
            ],

            dialog: false,
            admin: null,
            overlay: false
        }
    },
    computed:{
        ...mapState({  
            areas: state => state.areas,
            estatus: state => state.estatus,
        }),
    },
    watch: {
        options: {
            handler () {
                this.getDataFromApi()
            },
            deep: true,
        },
    },
    beforeMount(){
        this.getAreas();
    },
    methods: {
        ...mapActions([
            'getAreas'
        ]),
        async getDataFromApi () {
            try{
                this.menu = false
                this.loading = true
                this.overlay = true;
                const {page, itemsPerPage } = this.options
                this.query.name = this.search.trim() == '' ? null : this.search 
                let query = JSON.stringify(this.query);
                let {data} = await this.$axios.get(`/administrador?query=${query}&limit=${itemsPerPage}&page=${page}`);
                this.overlay = false;
                this.rows = data.data;
                this.count = data.count;
                this.loading = false;
            }catch(exception){
                this.overlay = false;
                this.loading = false;
                console.error(exception);
            }
        },
        open(page, row=null){
            this.$emit('page', {page:page, row:row})
        },
        async clear(){
            for(let key in this.query){
                this.query[key] = null;
            }
            this.menu = false;
            await this.getDataFromApi();
        },
        DialogRemove(row){
            this.admin = row;
            this.dialog = true
        },
        async remove(){
            try{
                this.overlay = true;
                let {data} = await this.$axios.delete(`administrador/${this.admin.id}/delete`)
                this.overlay = false;
                this.dialog = false;
                if(data.response){
                    await this.getDataFromApi();
                    return;
                }
            }catch(exception){
                this.overlay = false;
                console.error(exception)
            }
        }
    },
}
